import React from 'react';
import ReactDom from 'react-dom';
import {Tabs, TabPanel} from '../src/index.js';

ReactDom.render((
  <Tabs
    defaultActiveKey={0}
    onchange={this.handleChange}
  >
    <TabPanel name="1">
      1
    </TabPanel>

    <TabPanel name="2">
      2
    </TabPanel>
  </Tabs>
), document.getElementById('j-app'));



